@include font('Lato Light', 'lato-light-webfont');
@include font('Lato Regular', 'lato-regular-webfont');
@include font('Lato Semibold', 'lato-semibold-webfont');

@mixin lato-light {
	font-family: 'Lato Light', 'Trebuchet MS', Helvetica, sans-serif; }
@mixin lato-regular {
	font-family: 'Lato Regular', 'Trebuchet MS', Helvetica, sans-serif; }
@mixin lato-semibold {
	font-family: 'Lato Semibold', 'Trebuchet MS', Helvetica, sans-serif; }
