body {
  background: #FFF;
  font-family: 'Lato Regular', 'Trebuchet MS', Helvetica, sans-serif;
  font-size: 16px;
  color: #333;
}

.button {
  appearance: none;
  display: block;
  border: 1px solid;
  border-radius: 3px;
  background-color: transparent;
  font-size: 16px;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
  height: 48px;
  line-height: 48px;
  transition: all 0.2s ease;
}

.button_white {
  color: #fff;
  border-color: #fff;
}

.button_white:hover {
  color: #8e8e8e;
  background-color: #fff;
}

.button_black {
  color: #333;
  border-color: #333;
}

.button_black:hover {
  color: #fff;
  background-color: #333;
}

.button_gray {
  color: #8e8e8e;
  border-color: #8e8e8e;
}

.button_gray:hover {
  color: #fff;
  border-color: #fff;
  background-color: transparent;
}

.block {
  border: 1px solid #8e8e8e;
  padding: 50px 18px 30px;
  border-radius: 3px;
  color: #8e8e8e;
  position: relative;
  transition: all 0.2s ease;
}

.block:hover {
  color: #333;
  border-color: #333;
}

.block:hover .block__icon .icon {
  fill: #333;
}

.block__icon {
  display: block;
  position: absolute;
  top: -1.5em;
  left: 50%;
  transform: translateX(-50%);
  padding: 0 10px;
  background-color: #fff;
}

.block__icon .icon {
  width: 3em;
  height: 3em;
  fill: #8e8e8e;
  transition: all 0.2s ease;
}

.block__title {
  font-size: 16px;
  font-family: 'Lato Semibold', 'Trebuchet MS', Helvetica, sans-serif;
  color: #333;
  padding-bottom: 18px;
  margin-bottom: 18px;
  text-transform: uppercase;
  position: relative;
}

.block__title:after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  max-width: 50px;
  height: 1px;
  background-color: #8e8e8e;
}

.block__text p {
  margin-bottom: 10px;
}

.input {
  margin-bottom: 30px;
  position: relative;
}

.input.is-focused:before {
  width: 100%;
}

.input:before {
  content: "";
  display: block;
  width: 0px;
  height: 1px;
  background-color: #000;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 2;
  transition: 0.5s 0.2s ease;
}

.input input {
  width: 100%;
  border-bottom: 1px solid #8e8e8e;
  padding: 0 30px;
  height: 44px;
  font-family: 'Lato Regular', 'Trebuchet MS', Helvetica, sans-serif;
  font-size: 16px;
  color: #333;
  text-align: left;
  position: relative;
}

.input input::placeholder {
  font-size: 16px;
  color: #8e8e8e;
  transition: 0.3s 0.2s ease;
}

.input input:focus::placeholder {
  color: #333;
}

.input textarea {
  width: 100%;
  height: 44px;
  padding: 7px 30px;
  margin-bottom: -5px;
  resize: none;
  border: 0;
  border-bottom: 1px solid #8e8e8e;
  font-family: 'Lato Regular', 'Trebuchet MS', Helvetica, sans-serif;
  font-size: 16px;
}

.input textarea::placeholder {
  font-size: 16px;
  color: #8e8e8e;
  transition: 0.3s 0.2s ease;
}

.input textarea:focus {
  height: 100px;
}

.input textarea:focus::placeholder {
  color: #333;
}

.l {
  max-width: 1110px;
  margin: 0 auto;
  padding: 0 20px;
}

.header {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  background-color: #fff;
}

.header__in {
  text-align: center;
  font-size: 0;
  height: 80px;
}

.header__in:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.header__nav {
  display: inline-block;
  vertical-align: middle;
}

.header__nav-item {
  display: inline-block;
  vertical-align: middle;
  padding: 6px;
  margin-right: 15px;
}

.header__nav-link {
  color: #333;
  opacity: .5;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 16px;
  transition: opacity 0.2s ease;
}

.header__nav-link:hover, .header__nav-link.is-active {
  opacity: 1;
}

.footer {
  background-image: url("../img/footer-image.jpg");
  height: 300px;
  background-position: 50% 100%;
  background-repeat: no-repeat;
  background-size: contain;
  position: relative;
}

.footer .l:after {
  content: " ";
  display: table;
  clear: both;
}

.footer__in {
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  padding: 15px 0;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  color: #fff;
}

.footer__copyright {
  float: left;
  line-height: 48px;
}

.footer__copyright p {
  white-space: nowrap;
}

.footer__socials {
  float: right;
}

.footer__social {
  float: left;
  margin-left: 15px;
}

.footer__social a {
  display: block;
  border: 1px solid #fff;
  width: 50px;
  height: 48px;
  position: relative;
  transition: all 0.2s ease;
}

.footer__social a:hover {
  background-color: #fff;
}

.footer__social a:hover .icon {
  fill: #333;
  border-color: #333;
}

.footer__social .icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 1.4em;
  height: 1.5em;
  fill: #fff;
}

.section {
  padding: 47px 0;
}

.section__title {
  font-size: 30px;
  display: inline-block;
  position: relative;
  padding-top: 50px;
  margin-bottom: 90px;
  text-transform: uppercase;
}

.section__title:before {
  content: "";
  width: 2px;
  height: 50px;
  background-color: #333;
  display: block;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.section__in {
  text-align: center;
}

.section__in:after {
  content: " ";
  display: table;
  clear: both;
}

.section__text {
  line-height: 1.8;
}

.section__text p {
  margin-bottom: 10px;
}

.hero {
  margin-bottom: 47px;
  padding-top: 80px;
  height: calc(100vh - 80px);
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.hero .l {
  text-align: center;
  font-size: 0;
  height: 100%;
}

.hero .l:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.hero__in {
  display: inline-block;
  vertical-align: middle;
}

.hero__title {
  font-family: 'Lato Semibold', 'Trebuchet MS', Helvetica, sans-serif;
  color: #fff;
  font-size: 60px;
  margin-bottom: 10px;
}

.hero__text {
  color: #fff;
  margin-bottom: 50px;
  font-size: 16px;
}

.hero__button {
  display: inline-block;
}

.hero__button .button {
  width: 200px;
}

.about__in {
  text-align: left;
}

.about__title {
  margin-bottom: 35px;
}

.about__picture {
  width: 36.03604%;
  float: left;
}

.about__picture img {
  width: 100%;
}

.about__text {
  overflow: hidden;
  padding-left: 60px;
}

.advantages__blocks {
  display: flex;
  justify-content: space-between;
}

.advantages__blocks .block {
  width: 22.97297%;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
}

.gallery__item {
  width: 31.53153%;
  margin-right: 2.7027%;
  margin-bottom: 30px;
  padding-top: 30%;
  position: relative;
  height: 0;
}

.gallery__item:nth-of-type(3n) {
  margin-right: 0;
}

.gallery__item:hover .gallery__picture:before {
  opacity: 1;
}

.gallery__item:hover .button {
  opacity: 1;
}

.gallery__item .button {
  width: 70%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
  opacity: 0;
}

.gallery__picture {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.gallery__picture:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: .3;
  z-index: 2;
  transition: opacity 0.3s ease;
}

.contact__title {
  margin-bottom: 0;
  padding-left: 10px;
  padding-right: 10px;
  background-color: #fff;
  z-index: 2;
}

.contact__title:before {
  display: none;
}

.contact__form {
  border: 1px solid #8e8e8e;
  position: relative;
  top: -21px;
  padding: 56px 20px;
  max-width: 900px;
  margin: 0 auto;
}

.contact__form-in {
  max-width: 730px;
  margin: 0 auto;
}

.contact__form .button {
  width: 200px;
  margin: 0 auto;
}
